html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  overflow-x: hidden;
}

img {
  max-width: 100%;
  object-fit: cover;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  font-size: initial;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.section-title {
  font-weight: 700;
  font-size: 58px;
  line-height: 110%;
  letter-spacing: -2px;
  text-transform: uppercase;
  @include tablet {
    font-size: 45px;
  }
  @include tablet {
    font-size: 40px;
  }
}

.setion-subtitle {
  font-weight: 700;
  font-size: 32px;
  line-height: 110%;
  letter-spacing: -1px;
  text-transform: uppercase;
  @include tablet {
    font-size: 28px;
  }
  @include tablet {
    font-size: 24px;
  }
}

.descr {
  font-size: 16px;
  line-height: 29px;
  @include mobile {
    font-size: 14px;
    line-height: 20px;
  }
}

.text {
  font-size: 14px;
  line-height: 29px;
}

.btn {
  font-family: "Aileron", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: var(--color-light);
  background-color: transparent;
  border: 1px solid var(--color-light);
  border-radius: 30px;
  padding: 10px 35px;
  transition: color 0.2s, border 0.2s;
  &:hover {
    border: 1px solid var(--color-gray);
    color: var(--color-gray);
  }
  &--secondary {
    font-size: 14px;
    line-height: 17px;
    padding: 10px 40px;
    color: var(--color-black);
    border: 1px solid var(--color-black);
    transition: color 0.2s, border 0.2s;
    &:hover {
      border: 1px solid var(--color-shuttle);
      color: var(--color-shuttle);
    }
  }

  @include mobile {
    font-size: 14px;
    line-height: 18px;
  }
}
